import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {get, head, last, split, round} from "lodash"
import downloadIcon from "../../../assets/icons/download.svg"
import {NumericFormat} from "react-number-format";
import {useNavigate} from 'react-router-dom'
import femaleIcon from "../../../assets/icons/gender-female.svg"
import maleIcon from "../../../assets/icons/gender-male.svg"
import {useTranslation} from "react-i18next";
import {usePostQuery} from "../../../hooks/api";
import {ContentLoader, OverlayLoader} from "../../../components/loader";
import {REPORT_TYPES} from "../../../constants";
import {RefreshCcw} from "react-feather";
import Modal from "../../../components/modal";
import {Datepicker} from "../../../containers/form/components";
import Form from "../../../containers/form";
import dayjs from "dayjs";


const OutpatientReportsContainer = () => {
    const navigate = useNavigate()
    const {t} = useTranslation()
    const [open, setOpen] = useState(false)
    const {mutate: generateReportRequest, isLoading: isLoadingReport} = usePostQuery({listKeyId: KEYS.reports})
    const {mutate: refreshReportRequest, isLoading: isLoadingRefreshReport} = usePostQuery({listKeyId: KEYS.reports})
    const columns = [
        {
            title: t('Month'),
            key: 'month',
            render: ({value}) => <span className={'px-3'}>{value}</span>
        },
        {
            title: '<1',
            key: 'patient_groups.code',
            render: ({row}) => <div className={'text-left '}>
                <div
                    className={'px-3  pb-2.5 border-b border-dashed border-b-[#006D85] '}>
                <span
                    className={'bg-[#D9D9D9] inline-block rounded-[20px] px-2 py-1 text-xs font-semibold text-black mr-14'}>Нас.</span>
                    <NumericFormat
                        thousandSeparator={' '} displayType={'text'}
                        value={round(get(row, 'patient_groups[0].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5 px-3 '}><span
                    className={'bg-[#D9D9D9] inline-block rounded-[20px] px-2 py-1 text-xs font-semibold text-black mr-10 '}>Сумма.</span><NumericFormat
                    className={''} thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[0].payment', 0), 2)}/>
                </div>
            </div>,
        },
        {
            title: '1-5',
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[1].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                          value={round(get(row, 'patient_groups[1].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: '6-14',
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[2].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5'}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                         value={round(get(row, 'patient_groups[2].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: <span className={'inline-flex items-center'}>15-39F <img src={femaleIcon} alt="female"/></span>,
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[3].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5'}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                         value={round(get(row, 'patient_groups[3].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: <span className={'inline-flex items-start'}>15-39M <img src={maleIcon} alt="male"/></span>,
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85] '}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[4].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5'}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                         value={round(get(row, 'patient_groups[4].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: <span className={'inline-flex items-center'}>40-49F <img src={femaleIcon} alt="female"/></span>,
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[5].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                          value={round(get(row, 'patient_groups[5].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: <span className={'inline-flex items-start'}>40-49M <img src={maleIcon} alt="male"/></span>,
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[6].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                          value={round(get(row, 'patient_groups[6].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: '50-65',
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[7].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                          value={round(get(row, 'patient_groups[7].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: '65<',
            key: 'patient_groups',
            render: ({row}) => <div>
                <div
                    className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[8].count', 0), 2)}/>
                </div>
                <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                          value={round(get(row, 'patient_groups[8].payment', 0), 2)}/>
                </div>
            </div>
        },
        {
            title: <div className={'flex justify-around'}><span className={'mr-10 ml-3'}>{t("By groups")} </span><span
                className={'mr-3'}>{t("In count")}</span></div>,
            key: 'organization_level',
            render: ({row}) => <div className={'text-center'}>
                <div
                    className={"pb-2.5 border-b border-dashed border-b-[#006D85] relative after:absolute after:top-full after:content-[''] after:h-[50px]  after:border-r after:border-dashed after:border-r-[#006D85] after:w-[1px] after:left-1/2"}>
                    <NumericFormat displayType={'text'} thousandSeparator={" "} value={get(row, 'total_count', 0)}/>
                </div>
                <div className={'flex justify-around '}><span
                    className={'pt-2.5 w-1/2 '}><NumericFormat displayType={'text'} thousandSeparator={" "}
                                                               value={round(get(row, 'total_payment_by_group', 0), 2)}/></span><span
                    className={'pt-2.5 w-1/2 '}><NumericFormat displayType={'text'} thousandSeparator={" "}
                                                               value={round(get(row, 'total_payment', 0), 2)}/></span>
                </div>
            </div>,
        },

    ]
    const generateReport = () => {
        generateReportRequest({
            url: `${URLS.reports}/${REPORT_TYPES.patient_general_practitioner_report}`
        }, {
            onSuccess: () => {
                navigate('/reports')
            }
        })
    }
    const refreshReport = ({data: {month = new Date()} = {}}) => {
        refreshReportRequest({
            url: URLS.outpatientReports,
            attributes: {
                month: dayjs(month).format("YYYY-MM")
            }
        }, {
            onSuccess: () => {
                setOpen(false);
            }
        })
    }
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>{t("Outpatient Reports")}</Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        onClick={() => setOpen(true)}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg mr-3'}>
                        <RefreshCcw className={'mr-2.5'} size={20}/>
                        {t("Generate")}
                    </button>
                    <button
                        onClick={generateReport}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <img className={'mr-2.5'} src={downloadIcon} alt={'export'}/>
                        {t("Export")}
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    {isLoadingReport && <OverlayLoader/>}
                    <GridView
                        doubleRow
                        onRowClick={(_row) => navigate(`/dispensaries/outpatient-reports/${head(split(get(_row, 'month', ""), '/', 2))}/${last(split(get(_row, 'month', ""), '/', 2))}`)}
                        listKey={KEYS.outpatientReports} url={URLS.outpatientReports}
                        columns={columns}/>
                </div>
            </div>
            <Modal onClose={() => {
                setOpen(false);
            }}
                   classNames={'overflow-y-visible relative'}
                   title={''}
                   open={open}>
                {isLoadingRefreshReport && <ContentLoader/>}
                <Form onSubmit={refreshReport}>
                    <Datepicker property={{showMonthYearPicker: true}} dateFormat={'yyyy/MM'} name={'month'}
                                label={t('Month')}
                                params={{required: true}}/>
                    <button type={'submit'}
                            className={'w-full p-3.5 rounded-lg bg-primary block  text-white font-bold text-center h-12 mt-6'}>
                        {t('Generate')}
                    </button>
                </Form>
            </Modal>
        </div>
    );
};

export default OutpatientReportsContainer;